    <p className="firstLetter">Ὁ ἐν παντὶ καιρῷ καὶ πάσῃ ὥρᾳ ἐν οὐρανῷ καὶ ἐπὶ γῆς προσκυνούμενος καὶ δοξαζόμενος Χριστὸς ὁ Θεός, ὁ μακρόθυμος, ὁ πολυέλεος, ὁ πολυέσπλαγχνος, ὁ τοὺς δικαίους ἀγαπῶν καί τοὺς ἁμαρτωλοὺς ἐλεῶν, ὁ πάντας καλῶν πρὸς σωτηρίαν διά τῆς ἐπαγγελίας τῶν μελλόντων ἀγαθῶν , αὐτός Κύριε, πρόσδεξαι καὶ ἡμῶν ἐν τῇ ὥρᾳ ταύτῃ τὰς ἐντεύξεις καὶ ἴθυνον τὴν ζωὴν ἡμῶν πρὸς τὰς ἐντολάς σου. Τὰς ψυχὰς ἡμῶν ἁγίασον, τὰ σώματα ἅγνισον , τοὺς λογισμοὺς διόρθωσον, τὰς ἐννοίας κάθαρον καὶ ῥῦσαι ἡμᾶς ἀπὸ πάσης θλίψεως, κακῶν καὶ ὀδύνης. Τείχισον ἡμᾶς ἁγίοις σου Ἀγγέλοις, ἵνα τῇ παρεμβολῇ αὐτῶν φρουρούμενοι καὶ ὁδηγούμενοι καταντήσωμεν εἰς τὴν ἑνότητα τῆς πίστεως καὶ εἰς τὴν ἐπίγνωσιν τῆς ἀπροσίτου σου δόξης, ὅτι εὐλογητὸς εἶ εἰς τοὺς αἰῶνας τῶν αἰώνων. Ἀμήν.</p>

