    <p className="firstLetter">Ὁ κατοικῶν ἐν βοηθείᾳ τοῦ Ὑψίστου, ἐν σκέπῃ τοῦ Θεοῦ τοῦ οὐρανοῦ αὐλισθήσεται. Ἐρεῖ τῷ Κυρίῳ· Ἀντιλήπτωρ μου εἶ, καὶ καταφυγή μου, ὁ Θεός μου, καὶ ἐλπιῶ ἐπ' αὐτόν. Ὅτι αὐτὸς ῥύσεταί σε ἐκ παγίδος θηρευτῶν, καὶ ἀπὸ λόγου ταραχώδους. Ἐν τοῖς μεταφρένοις αὐτοῦ ἐπισκιάσει σοι, καὶ ὑπὸ τὰς πτέρυγας αὐτοῦ ἐλπιεῖς, ὅπλῳ κυκλώσει σε ἡ ἀλήθεια αὐτοῦ. Οὐ φοβηθήσει ἀπὸ φόβου νυκτερινοῦ, ἀπὸ βέλους πετομένου ἡμέρας. Ἀπὸ πράγματος ἐν σκότει διαπορευομένου, ἀπὸ συμπτώματος καὶ δαιμονίου μεσημβρινοῦ. Πεσεῖται ἐκ τοῦ κλίτους σου χιλιάς, καὶ μυριὰς ἐκ δεξιῶν σου, πρὸς σὲ δὲ οὐκ ἐγγιεῖ. Πλὴν τοῖς ὀφθαλμοῖς σου κατανοήσεις, καὶ ἀνταπόδοσιν ἁμαρτωλῶν ὄψει. Ὅτι σύ, Κύριε, ἡ ἐλπίς μου, τὸν Ὕψιστον ἔθου καταφυγήν σου. Οὐ προσελεύσεται πρὸς σὲ κακά, καὶ μάστιξ οὐκ ἐγγιεῖ ἐν τῷ σκηνώματί σου. Ὅτι τοῖς Ἀγγέλοις αὐτοῦ ἐντελεῖται περὶ σοῦ, τοῦ διαφυλάξαι σε ἐν πάσαις ταῖς ὁδοῖς σου. Ἐπὶ χειρῶν ἀροῦσί σε, μήποτε προσκόψῃς πρὸς λίθον τὸν πόδα σου. Ἐπὶ ἀσπίδα καὶ βασιλίσκον ἐπιβήσει, καὶ καταπατήσεις λέοντα καὶ δράκοντα, Ὅτι ἐπ' ἐμὲ ἤλπισε, καὶ ῥύσομαι αὐτόν, σκεπάσω αὐτόν, ὅτι ἔγνω τὸ ὄνομά μου. Κεκράξεται πρός με, καὶ εἰσακούσομαι αὐτοῦ, μέτ' αὐτοῦ εἰμι ἐν θλίψει, ἐξελοῦμαι αὐτόν, καὶ δοξάσω αὐτόν. Μακρότητα ἡμερῶν ἐμπλήσω αὐτόν, καὶ δείξω αὐτῷ τὸ σωτὴριόν μου</p>

