import React from "react";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import FormControl from "@material-ui/core/FormControl";
import ColorLensIcon from "@material-ui/icons/ColorLens";
import FontDownloadIcon from "@material-ui/icons/FontDownload";
import GTranslateIcon from "@material-ui/icons/GTranslate";
import Button from "@material-ui/core/Button";
import Grid from "@material-ui/core/Grid";
import ToggleButton from "@material-ui/lab/ToggleButton";
import ToggleButtonGroup from "@material-ui/lab/ToggleButtonGroup";
import { ButtonGroup } from "@material-ui/core";
import Divider from '@material-ui/core/Divider';
import Switch from '@material-ui/core/Switch';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import { DrawerProps } from "./types";
import { Link } from "@material-ui/core";

const localePriest = {
  el: 'Ἱερεὺς ',
  en:  'Priest '
}

const Drawer = ({
  classes,
  locale,
  handleChangeContext,
  context,
  handleChangeLocale,
  onGoTo
}: DrawerProps) => (
  <List>
    <div className={classes.drawerSpacer}/>
      {Object.keys(locale).map(key => (
        <ListItem button key={key} onClick={() => onGoTo(key)}>
          <Link >
            {locale[key]}
          </Link>
        </ListItem>
      ))}

    <Divider />

    <ListItem key="theme">
      <ListItemIcon>
        <ColorLensIcon />
      </ListItemIcon>
      <FormControl className={classes.formControl}>
        <ButtonGroup>
          <Button
            aria-label="light"
            className={classes.buttonThemeLight}
            name="theme"
            value="0"
            onClick={handleChangeContext}
          >
            {" "}
          </Button>
          <Button
            aria-label="tan"
            className={classes.buttonThemeTan}
            name="theme"
            value="1"
            onClick={handleChangeContext}
          >
            {" "}
          </Button>
          <Button
            aria-label="dark"
            className={classes.buttonThemeDark}
            name="theme"
            value="2"
            onClick={handleChangeContext}
          >
            {" "}
          </Button>
        </ButtonGroup>
      </FormControl>
    </ListItem>

    <ListItem key="font">
      <ListItemIcon>
        <FontDownloadIcon />
      </ListItemIcon>
      <FormControl className={classes.formControl}>
        <Grid
          container
          direction="row"
          justify="space-between"
          alignItems="center"
          // className={classes.navigation}
        >
          <Grid item>
            <Button
              variant="outlined"
              color="default"
              size="small"
              name="fontSize"
              className={classes.buttonFont}
              value={context.state.fontSize - 1}
              onClick={handleChangeContext}
            >
              -
            </Button>
          </Grid>
          <Grid item>
            {context.state.fontSize}px
          </Grid>
          <Grid item>
            <Button
              variant="outlined"
              color="default"
              size="small"
              name="fontSize"
              value={context.state.fontSize + 1}
              className={classes.buttonFont}
              onClick={handleChangeContext}
            >
              +
            </Button>
          </Grid>
        </Grid>
      </FormControl>
    </ListItem>

    <ListItem key="locale">
      <ListItemIcon>
        <GTranslateIcon />
      </ListItemIcon>
      <FormControl className={classes.formControl}>
        <ToggleButtonGroup
          value={context.state.locale}
          exclusive
          aria-label="Locale"
        >
          <ToggleButton
            value="el"
            aria-label="ellinika"
            onClick={() => handleChangeLocale("el")}
          >
            <img src="assets/el.gif" alt="el" />
          </ToggleButton>
          <ToggleButton
            value="en"
            aria-label="english"
            onClick={() => handleChangeLocale("en")}
          >
            <img src="assets/en.gif" alt="el" />
          </ToggleButton>
          <ToggleButton
            value="ru"
            aria-label="russian"
            disabled
          >
            <img src="assets/ru.png" alt="ru" />
          </ToggleButton>
        </ToggleButtonGroup>
      </FormControl>
    </ListItem>

    <ListItem key="priest">
      <ListItemIcon>
        <GTranslateIcon />
      </ListItemIcon>
      <FormControl >
        <FormControlLabel
          value="start"
          control={<Switch
            checked={context.state.priest}
            onChange={handleChangeContext}
            color="default"
            name="priest"
            size="small"
            inputProps={{ 'aria-label': 'primary checkbox' }}
          />}
          label={localePriest[context.state.locale]}
          labelPlacement="start"
        />
      </FormControl>
    </ListItem>

  </List>
);

export default Drawer;
