import React, { useState, useEffect } from "react";
import Context from "./Context";
import { StateProp, ProviderProp } from "./types";
import { LightTheme, DarkTheme, TanTheme } from "../../themes";

const defaultName = "proseuxitarion";
const defaultState: StateProp = {
  theme: 0,
  fontSize: 18,
  locale: "el",
  priest: false,
};

const GlobalValuesProvider = ({ children }: ProviderProp) => {
  // STATE
  const storage = {
    ...defaultState,
    ...JSON.parse(localStorage.getItem(defaultName)),
  };
  if (!storage) localStorage.setItem(defaultName, JSON.stringify(defaultState));
  const [state, setFinalState] = useState(storage || defaultState);

  const setState = (values: StateProp) => {
    localStorage.setItem(defaultName, JSON.stringify(values));
    window.scrollTo({
      top: 0,
      behavior: "smooth",
    });
    setFinalState(values);
  };

  // THEME
  const [theme, setTheme] = useState(LightTheme);

  useEffect(() => {
    let currentTheme = LightTheme;
    if (state.theme === 1) currentTheme = TanTheme;
    if (state.theme === 2) currentTheme = DarkTheme;
    setTheme(currentTheme);
  }, [state.theme]); // eslint-disable-line


  return (
    <Context.Provider value={{ state, setState, theme }}>
      {children}
    </Context.Provider>
  );
};

export default GlobalValuesProvider;
