import React, { useContext } from "react"; 
import { useLocation, useHistory } from "react-router-dom";
import CssBaseline from "@material-ui/core/CssBaseline";
import { ThemeProvider } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Hidden from "@material-ui/core/Hidden";
import IconButton from "@material-ui/core/IconButton";
import Drawer from "@material-ui/core/Drawer";
import MenuIcon from "@material-ui/icons/Menu";
import Context from "../../components/App/Context";
import useStyles from "./styles";
import DrawerComponent from "../Header/Drawer";
import { EventType } from "./types";
import locale from "./locale";

const Container = () => {
  const history = useHistory();
  const location = useLocation();
  const { pathname } = location;
  const classes = useStyles();
  const context = useContext(Context);
  const container = window !== undefined ? () => document.body : undefined;
  const [mobileOpen, setMobileOpen] = React.useState(false);

  const title = (pathname==='/') ? locale[context.state.locale].home : locale[context.state.locale][pathname.replaceAll('/','')]

  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };

  const handleChangeContext = (event: EventType) => {
    if (event.target.name === "fontSize") {
      context.setState({
        ...context.state,
        fontSize: Number(event.target.value),
      });
    } else if (event.target.name === "theme") {
      context.setState({
        ...context.state,
        theme: Number(event.target.value),
      });
    } else if (event.target.name === "priest") {
      context.setState({
        ...context.state,
        priest: !context.state.priest,
      });
    } else {
      context.setState({
        ...context.state,
        [event.target.name]: event.target.value,
      });
    }
  };

  const handleChangeLocale = (loc: "el" | "en") => {
    context.setState({
      ...context.state,
      locale: loc,
    });
  };

  const onGoTo = (path: string) => {
    history.push(`/${path}`);
    window.scrollTo({
      top: 0,
      behavior: "smooth",
    });
    setMobileOpen(false)
  }
  
  return (
    <div className={classes.root}>
    <CssBaseline />
    <ThemeProvider theme={context.theme}>
      <AppBar position="fixed" color="default" className={classes.appBar}>
        <Toolbar>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            edge="start"
            onClick={handleDrawerToggle}
            className={classes.menuButton}
          >
            <MenuIcon />
          </IconButton>
            <Typography variant="h6" noWrap>
              {title}
            </Typography>
        </Toolbar>
      </AppBar>
      <nav className={classes.drawer} aria-label="nav">
        {/* The implementation can be swapped with js to avoid SEO duplication of links. */}
        <Hidden smUp implementation="css">
          <Drawer
            container={container}
            variant="temporary"
            anchor="left"
            open={mobileOpen}
            onClose={handleDrawerToggle}
            classes={{
              paper: classes.drawerPaper,
            }}
            ModalProps={{
              keepMounted: true, // Better open performance on mobile.
            }}
          >
            {DrawerComponent({
              classes,
              locale: locale[context.state.locale],
              handleChangeContext,
              context,
              handleChangeLocale,
              onGoTo
            })}
          </Drawer>
        </Hidden>
        <Hidden xsDown implementation="css">
          <Drawer
            classes={{
              paper: classes.drawerPaper,
            }}
            variant="permanent"
            open
          >
            {DrawerComponent({
              classes,
              locale: locale[context.state.locale],
              handleChangeContext,
              context,
              handleChangeLocale,
              onGoTo
            })}
          </Drawer>
        </Hidden>
      </nav>
      </ThemeProvider>
      </div>
  );
};

export default Container;
