import React, { useContext } from "react";
import CssBaseline from "@material-ui/core/CssBaseline";
import { ThemeProvider } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import Context from "../../components/App/Context";
import useStyles from "./styles";
import Home from '../../components/Home'
import ProimiakiEl from '../../components/Proimiaki/el'
import OrthrosEl from '../../components/Orthros/el'
import Ora1El from '../../components/Ora1/el'
import Ora3El from '../../components/Ora3/el'
import Ora6El from '../../components/Ora6/el'
import Ora9El from '../../components/Ora9/el'
import EsperinosEl from '../../components/Esperinos/el'
import MetalipseosEl from '../../components/Metalipseos/el'
import Apodeipno from '../../components/Apodeipno'
import Parakleisis from '../../components/Parakleisis'
import PsaltirionEl from '../../components/Psaltirion/el'

const localePriest = {
  el: 'Ἱερεὺς ',
  en:  'Priest '
}

const Container = ({ comp }: any) => {
  const classes = useStyles();
  const context = useContext(Context);

  let red = context.theme.palette.error.main
  let grey = context.theme.palette.text.disabled
  const { locale, priest } = context.state

  return (
    <div className={classes.root}>
    <CssBaseline />
    <ThemeProvider theme={context.theme}>
      <Paper 
        className={classes.content}
        style={{
          fontSize: context.state.fontSize,
        }}
      >
        {comp==='home' && React.createElement(Home[locale], {red, grey})}
        {comp==='proimiaki' && <ProimiakiEl red={red} grey={grey} />}
        {comp==='orthros' && <OrthrosEl red={red} grey={grey}  />}
        {comp==='ora1' && <Ora1El priest={priest} localePriest={localePriest[locale]} red={red} grey={grey} />}
        {comp==='ora3' && <Ora3El priest={priest} localePriest={localePriest[locale]} red={red} grey={grey} />}
        {comp==='ora6' && <Ora6El priest={priest} localePriest={localePriest[locale]} red={red} grey={grey} />}
        {comp==='ora9' && <Ora9El priest={priest} localePriest={localePriest[locale]} red={red} grey={grey} />}
        {comp==='esperinos' && <EsperinosEl priest={priest} localePriest={localePriest[locale]} red={red} grey={grey} />}
        {comp==='metalipseos' && <MetalipseosEl priest={priest} localePriest={localePriest[locale]} red={red} grey={grey} />}
        {comp==='apodeipno' && React.createElement(Apodeipno[locale], {red, grey })}
        {comp==='psaltirion' && <PsaltirionEl priest={priest} localePriest={localePriest[locale]} red={red} grey={grey}  />}
        {comp==='parakleisis' && React.createElement(Parakleisis[locale], {priest, localePriest: localePriest[locale], red, grey })}
      </Paper>
      </ThemeProvider>
    </div>
  );
};

export default Container;
